import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Address from '@/components/Address'
export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/Address',
      name: 'Address',
      component: Address
    }
  ]
})



